<i18n>
{
  "en": {
    "Feedback": "Feedback",
    "SubmissionFailure": "Submission failure",
    "describe": "Please check whether the network is normal and try again.",
    "OK": "OK"
  },
  "zh": {
    "Feedback": "意见反馈",
    "SubmissionFailure": "提交失败",
    "describe": "请检查网络是否正常，再次重试！",
    "OK": "完成"
  }
}
</i18n>

<template>
  <div class="feedbackFailure">
    <head-top :title='$t("Feedback")' :custom-back="true" @goBackHander="goHandle"></head-top>
    <div class="container">
      <img src="../../assets/feedback_images/fail.png" alt="">
      <strong>{{ $t("SubmissionFailure") }}</strong>
      <span>{{ $t("describe") }}</span>
      <button @click="goHandle">{{ $t("OK") }}</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import headTop from '../../components/Header/index'
// import { routerGo } from '../../utils/router.js'
export default {
  name: 'feedbackFailure',
  data () {
    return {}
  },

  components: {
    headTop
  },

  methods: {
    ...mapActions([
      'sidebarSwitch'
    ]),

    goHandle () {
      this.$router.back()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.feedbackFailure{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    padding-top: 115px;
    img{
      height: 180px;
      display: block;
      margin: 0 auto 35px auto;
    }
    strong{
      font-size: 36px;
      display: block;
    }
    span{
      .wh(520px, 100px);
      display: block;
      font-size: 30px;
      margin: 26px auto 0 auto;
    }
    button{
      .wh(690px, 100px);
      background: @themeColor;
      border-radius: 10px;
      font-size: 30px;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -345px;
      box-shadow: 0px 4px 25px rgba(255, 219, 0, 0.6);
      color: #5A5A57;
    }
  }
}
</style>



